import React, { Component } from 'react';
import {Link} from 'react-router-dom';
import schoolLogo from '../images/logo.png';


class SideNav extends Component {
  render() {
    
    return (
      <div className='hide-on-med-and-down'>
        <div className='sideNav'>
              <div className=" center">
                <img className="imageSetting" src={schoolLogo} alt="school"/>
                <h6 className="white-text bold">UMAIR PUBLIC SCHOOL</h6>
                {/*<div className="divider">this is dive</div>
                */}
                <hr/>
              </div>
            <Link to='/'><i class="fas fa-home"></i> Home</Link>
            <Link to="/students"><i class="fas fa-user-graduate"></i> Students</Link>
            <Link to="/teachers"><i class="fas fa-chalkboard-teacher"></i> Teachers</Link>
            <Link to="/classes"><i class="fas fa-archway"></i> Classes</Link>
            <Link to="/parents"><i class="fas fa-people-carry"></i> Parents</Link>
          </div>
      </div>
    );
  }
}

export default SideNav;
