import React, { Component } from 'react';
import logo from "../images/images.png";
import M from 'materialize-css';
import Sidenav from './SideNav';
import { Link } from 'react-router-dom';
import schoolLogo from "../images/logo.png"

class Header extends Component {
    componentDidMount() {
        var elems = document.querySelectorAll('.sidenav');
    var instances = M.Sidenav.init(elems);

    }
    render() {
        return (
            <div >
                <nav className='headerManual'>
                    <a href="#" data-target="slide-out" class="sidenav-trigger"><i class="material-icons black-text">menu</i></a>
                    <div className="nav-wrapper container">
                        <a href="#" className="brand-logo black-text"><h5>{this.props.name}</h5></a>
                    </div>
                </nav>
                <ul id="slide-out" className="sidenav">

                    <div className=" center">
                <img className="imageSetting" src={schoolLogo} alt="school"/>
                <h6>UMAIR PUBLIC SCHOOL</h6>
                
              </div>
                    <hr/>
                    <li><Link to='/'><i className="material-icons iconSetting">account_box</i>Home</Link></li>
                    <li><Link to="/students"><i className="material-icons iconSetting">child_care</i>Students</Link></li>
                    <li><Link to="/teachers"><i className="material-icons iconSetting">assignment_ind</i>Teachers</Link></li>
                    <li><Link to="/classes"><i className="material-icons iconSetting">domain</i>Classes</Link></li>
                    <li><Link to="/parents"><i className="material-icons iconSetting">group</i>Parents</Link></li>

                </ul>
            </div>
        );
    }
}

export default Header;