import React, { Component } from 'react';
import Header from './Header';
import M from "materialize-css";

class AddTeacher extends Component {

    render() {

        let addTeacher = () =>{

            let name = document.getElementById("tname").value;
            let fname = document.getElementById("father_name").value;
            let DOB = document.getElementById("DOB").value;
            let mobile = document.getElementById("mobile").value;
            let address = document.getElementById("address").value;
            let email = document.getElementById("email").value;
            let qualification = document.getElementById("qualification").value;
            let pay = document.getElementById("pay").value;

            if(!name || !fname || !DOB || !mobile || !address || !email || !qualification || !pay ){
                return  M.toast({html: 'Write Data In All Feilds !'});
            }
            else{
                let teacher = {
                    name : name,
                    fname : fname,
                    DOB : DOB,
                    mobile : mobile,
                    address :address,
                    email : email,
                    qualification : qualification,
                    pay : pay
                }
    
                var options =  {
                    method :'POST',
                    body : JSON.stringify(teacher),
                    headers : {
                        'Content-Type' : "application/json"
                    }
                }
                fetch('/addTeacher',options)
                .then((res) => res.json())
                .then((json) => {
                    console.log(json);
                })
                .catch((err) => console.log(err))
    
                
                document.getElementById("tname").value = null;
                document.getElementById("father_name").value = null;
                document.getElementById("DOB").value = null;
                document.getElementById("mobile").value = null;
                document.getElementById("address").value = null;
                document.getElementById("qualification").value = null;
                document.getElementById("email").value = null;
                document.getElementById("pay").value = null;
    
                M.toast({html: 'Teacher is Added'});
            }

            
            
        }

        return (
            <div>
                <Header name="Add A New Teacher" />
                <div class="row container">
                    <h3>Enter Teacher's Details</h3>
                    <form className="col s12 ">
                        <div class="row">
                            <div class="input-field col s6">
                                <input id="tname" type="text" class="validate"/>
                                <label for="tname">Teachers Name</label>
                            </div>
                                <div class="input-field col s6">
                                    <input id="father_name" type="text" className="validate"/>
                                        <label for="faher_name">Fathers Name</label>
                            </div>
                        </div>
                        <div className="row">
                            <div className="input-field col s6">
                                <input id="DOB" type="date" className="validate"/>
                                <label for="DOB">Date Of Birth</label>
                            </div>
                            <div className="input-field col s6">
                                <input id="mobile" type="number" className="validate"/>
                                <label for="mobile">Mobile</label>
                            </div>
                        </div>
                        <div className="row">
                            <div className="input-field col s12">
                                <input id="address" type="text" className="validate"/>
                                <label for="address">Home Address</label>
                            </div>
                        </div>
                        <div class="row">
                            <div className="input-field col s6">
                                <input id="email" type="email" className="validate"/>
                                <label for="email">Email</label>
                            </div>
                        </div>
                        <div class="row">
                            <div className="input-field col s6">
                                <input id="qualification" type="text" className="validate"/>
                                <label for="qualification">Qualification</label>
                            </div>
                            <div className="input-field col s6">
                                <input id="pay" type="number" className="validate"/>
                                <label for="pay">Pay</label>
                            </div>
                        </div>                        
                    </form>
                    <a class="waves-effect waves-light btn blue" onClick={addTeacher}>Add Teacher</a>
                </div>


                                </div>
                                )
    }

}

export default AddTeacher;