import React, { Component } from 'react';
import logo from './logo.svg';
import './css/materialize.css';
import { BrowserRouter, Switch, Route, Link } from 'react-router-dom'
import './App.css';


import Home from './components/Home.js';
import Students from './components/Students';
import SideNav from './components/SideNav';
import Teachers from './components/Teachers';
import Parents from './components/Parents';
import Classes from './components/Classes';
import AddStudent from './components/AddStudent';
import TeacherDetail from './components/TeacherDetail';
import AddTeacher from './components/AddTeacher';
import ExpelStudent from './components/ExpelStudent';
import ExpelTeacher from './components/ExpelTeacher';
import PayDetail from './components/PayDetail';
import StudentDetail from './components/StudentDetail';
import StudentFee from './components/StudentFee';

class App extends Component {
  render() {
    return (
      <BrowserRouter>
        <div>
          <SideNav />
          <Switch>
            <div className='right'>
              


              <Route path='/students' component={Students} />
              <Route path='/parents' component={Parents}/>
              <Route path='/classes' component={Classes}/>
              <Route path='/teachers' component={Teachers}/>
              <Route path='/addStudent' component={AddStudent}/>
              <Route path='/teacherDetail' component={TeacherDetail}/>
              <Route path='/addTeacher' component={AddTeacher}/>
              <Route path='/expelStudent' component={ExpelStudent}/>
              <Route path='/expelTeacher' component={ExpelTeacher}/>
              <Route path='/payDetail' component={PayDetail}/>
              <Route path='/studentDetail' component={StudentDetail}/>
              <Route path='/studentFee' component={StudentFee}/>

               <Route exact path='/' component={Home} />
            </div>
          </Switch>
        </div>


      </BrowserRouter>
    );
  }
}

export default App;
