import React, { Component } from 'react';
import teacher_pic from '../images/female-teacher.jpg'; 
import student_pic from '../images/student.jpg'; 
import class_pic from '../images/class.jpg'; 
import timetable_pic from '../images/timetable.jpg'; 
import parents_pic from '../images/parents.png'; 
import { Link } from 'react-router-dom';
import Header from './Header.js';



class Home extends Component {



  render() {

    let data = [
      {
        name : 'Teachers',
        number : 8,
        image : teacher_pic,
        link : '/teachers'
      },
      {
        name : 'Students',
        number : 9,
        image : student_pic,
        link : '/students'
      },
      {
        name : 'Classes',
        number : 8,
        image : class_pic,
        link : '/classes'
      },
      {
        name : 'Time Table',
        number : 111,
        image : timetable_pic,
        link : '/timetable'
      },
      {
        name : 'Parents',
        number : 9,
        image : parents_pic,
        link : '/parents'
      }
    ]

    let teachesNumber = 8;
    // let classes = 10;
    // let studentsNumber = 8;
    // let Number = 8;


    return (
      <div>
        <Header name="Home"/>
        <div className="container row">
        
{data.map(
  (value,index) =>{
    return (
      <div className="col s12 m5 l4 cardSetting center">
          <div className="card horizontal cardSetting">
            <div className="card-image">
              <img className="cardImage" src={value.image} />
            </div>
            <div className="card-stacked">
              <div className="card-content">
                <h1>{value.number}</h1>
              </div>
              <div className="card-action">
                <Link to={value.link} className="#">{value.name}</Link>
              </div>
            </div>
          </div>
        </div>
    )
  }
)}
      </div>
      </div>

      
      

    );
  }
}

export default Home;
