import React, { Component } from 'react';
import Header from "./Header";
import M from 'materialize-css';


class ExpelStudent extends Component {
    
  render() {

    let expelStudent = () =>{
      let Class = document.getElementById('Class').value;
      let rollNo = document.getElementById('rollNo').value;

      if(!Class || !rollNo){
       return M.toast({html: 'Enter Both fields to Expel Student'});
      }
      else{
      var options =  {
          method :'POST',
          body : JSON.stringify({class : Class , rollNo : rollNo}),
          headers : {
              'Content-Type' : "application/json"
          }
      }

      fetch('/expelStudent',options)
      .then((res)=> res.text())
      .then((json) => {

          M.toast({html: json})
      })
      .catch((err) => console.log(err))

      
      document.getElementById("Class").value = null;
      document.getElementById("rollNo").value = null;

      
      }
    }

    return (
      <div >
        <Header name="Expel Student"/>
        <div className="row container">
                    <h3>Enter Class and Roll No. To Expel</h3>
                    <form className="col s12 ">
                        <div className="row">
                            <div className="input-field col s6">
                                <input id="Class" type="text" className="validate"/>
                                <label for="Class">Class</label>
                            </div>
                                <div className="input-field col s6">
                                    <input id="rollNo" type="text" className="validate"/>
                                        <label for="rollNo">Roll Number</label>
                            </div>
                        </div>                        
                    </form>
                    <a className="waves-effect waves-light btn red" onClick={expelStudent}>Expel Student</a>
                </div>
      </div>
    );
  }
}

export default ExpelStudent;
