import React, { Component } from 'react';
import Header from './Header'
import M from 'materialize-css'


class StudentFee extends Component {
    state = {
      students : []
    }
  render() {

    let showFeeDetails = () =>{
      let Class = document.getElementById('Class').value;

      if(!Class){
       return M.toast({html: 'Enter class for fee details'});
      }
      else{
      var options =  {
          method :'POST',
          body : JSON.stringify({class : Class}),
          headers : {
              'Content-Type' : "application/json"
          }
      }

      fetch('/showDetails',options)
      .then((res) => res.json())
      .then((json) => {
          this.setState({students : json});
          M.toast({html: 'Showing details'})
          console.log(this.state.students)
      })
      .catch((err) => console.log(err))

      
      document.getElementById("Class").value = null;

      
      }
    }

    
    
    return (
      <div >
        <Header name="Students Fee"/>
        <div className="container">
        <form className="col s12 ">
                <div className="row">
                    <div className="input-field col s6">
                        <input id="Class" type="text" className="validate"/>
                        <label for="Classs">Class</label>
                    </div>
                </div>    
                <a className="waves-effect waves-light btn blue" onClick={showFeeDetails}>Show details</a>                    
            </form>
            <h3>Detail of class</h3>
            <table>
            <thead>
            <tr>
                <th>student Name</th>
                <th>Class</th>
                <th>Roll Number</th>
                <th>Mobile</th>
                <th>Fee</th>
            </tr>
            </thead>

            <tbody>

            {this.state.students.map((value,index)=>{
                return (
                <tr key={index}>
                <td>{value.name}</td>
                <td>{value.class}</td>
                <td>{value.rollNo}</td>
                <td >{value.mobile}</td>
                <td >{value.fee}</td>
                </tr> 
                )
            })}
            
            
            </tbody>
            </table>
        </div>
      </div>
    );
  }
}

export default StudentFee;
