import React, { Component } from 'react';
import { Link } from 'react-router-dom';


import Header from './Header.js';


import details_pic from '../images/details.jpg'; 
import pay_pic from '../images/pay.jpg'; 
import teacher_pic from '../images/teacher.jpg'; 
import teacher_expel from '../images/expelteacher.jpg'; 



class Teachers extends Component {



  render() {

    let data = [
      {
        name : 'Personal Deatails',
        details : "Teacher's Details",
        image : details_pic,
        link : '/teacherDetail'
      },
      {
        name : 'Pay Details',
        details : 'Pay details of Teacher',
        image : pay_pic,
        link : '/payDetail'
      },
      {
        name : 'Add Teacher',
        details : 'Add A New Teacher',
        image : teacher_pic,
        link : '/addTeacher'
      },
      {
        name : 'Expel Teacher',
        details : 'Expel a Teacher',
        image : teacher_expel,
        link : '/expelTeacher'
      }
    ]

  


    return (
      <div>
        <Header name="TEACHER"/>
        <div className="container row">
        
{data.map(
  (value,index) =>{
    return (
      <div className="col s12 m5 l4 cardSetting center">
          <div className="card horizontal cardSetting">
            <div className="card-image">
              <img className="cardImage" src={value.image} />
            </div>
            <div className="card-stacked">
              <div className="card-content">
                <p>{value.details}</p>
              </div>
              <div className="card-action">
                <Link to={value.link} className="#">{value.name}</Link>
              </div>
            </div>
          </div>
        </div>
    )
  }
)}
      </div>
      </div>

      
      

    );
  }
}

export default Teachers;
