import React, { Component } from 'react';
import Header from './Header'
import M from 'materialize-css'


class PayDetail extends Component {
    
  state = {
    teachers : []
  }

  render() {

    let showPayDetail = () =>{
  

      
    
      var options =  {
          method :'GET',
          headers : {
              'Content-Type' : "application/json"
          }
      }

      fetch('/getTeachers',options)
      .then((res) => res.json())
      .then((json) => {
          this.setState({teachers : json});
          M.toast({html: 'Showing details'})
          console.log(this.state.teachers)
      })
      .catch((err) => console.log(err))

      
      
      }

    
    
    return (
      <div >
        <Header name="Pay Detail"/>
        <div className="container">
          <a className="waves-effect waves-light btn blue"  onClick={showPayDetail}>Show Pay Detail</a><br/><hr/><br/>
            <h3>Detail of Teachers Pay</h3>
            <table>
            <thead>
            <tr>
                <th>Teachers Name</th>
                <th>Qualification</th>
                <th>Pay</th>
                <th>Mobile</th>
            </tr>
            </thead>

            <tbody>

            {this.state.teachers.map((value,index)=>{
                return (
                <tr key={index}>
                <td>{value.name}</td>
                <td>{value.qualification}</td>
                <td>{value.pay}</td>
                <td >{value.mobile}</td>
                </tr> 
                )
            })}
            
            
            </tbody>
            </table>
        </div>
      </div>
    );
  }
}

export default PayDetail;
