import React, { Component } from 'react';
import Header from './Header';
import M from "materialize-css"

class Parents extends Component {
    state = {
      parent : []
    }
  render() {
    

    let showParent = () =>{
      let Class = document.getElementById('Class').value;
      let rollNo = document.getElementById('rollNo').value;

      if(!Class || !rollNo){
       return M.toast({html: 'Enter Both Inputs for details'});
      }
      else{
      var options =  {
          method :'POST',
          body : JSON.stringify({class : Class , rollNo : rollNo}),
          headers : {
              'Content-Type' : "application/json"
          }
      }

      fetch('/showStudent',options)
      .then((res) => res.json())
      .then((json) => {
          this.setState({ parent : json});
          M.toast({html: 'Showing details'})
          console.log(this.state.parent)
      })
      .catch((err) => console.log(err))

      
      document.getElementById("Class").value = null;
      document.getElementById("rollNo").value = null;

      
      }
    }

    
    return (

      <div >
          <Header name="PARENT"/>
          <div className="row container">
            <h3>Enter Class and Roll No. of student for parents detail</h3>
            <form className="col s12 ">
              <div className="row">
                <div className="input-field col s6">
                    <input id="Class" type="text" className="validate"/>
                    <label for="Class">Class</label>
                </div>
                    <div className="input-field col s6">
                        <input id="rollNo" type="text" className="validate"/>
                            <label for="rollNo">Roll Number</label>
                </div>
             </div>                        
            </form>
            <a className="waves-effect waves-light btn blue" onClick={showParent}>Parents detail</a>
            <br/>
            <hr/>
            <table>
              <thead>
                <tr>
                    <th>Student Name</th>
                    <th>Parent Name</th>
                    <th>Mobile Number</th>
                    <th>Address</th>
                </tr>
              </thead>

              <tbody>
              {this.state.parent.map((value,index)=>{
                  return (
                    <tr>
                    <td>{value.name}</td>
                    <td>{value.fname}</td>
                    <td>{value.mobile}</td>
                    <td>{value.address}</td>
                  </tr> 
                  )
              })}
                
              
              </tbody>
            </table>
          </div>
                
      </div>
    );
  }
}

export default Parents;
