import React, { Component } from 'react';
import Header from './Header';
import M from 'materialize-css';

class AddStudent extends Component {

    render() {
        let addStudent = () =>{

            let name = document.getElementById("name").value;
            let fname = document.getElementById("father_name").value;
            let DOB = document.getElementById("DOB").value;
            let mobile = document.getElementById("mobile").value;
            let address = document.getElementById("address").value;
            let Class = document.getElementById("class").value;
            let rollNo = document.getElementById("roll_no").value;
            let fee = document.getElementById("fee").value;

            if(!name || !fname || !DOB || !mobile || !address || !Class || !rollNo || !fee ){
                return  M.toast({html: 'Write Data In All Feilds !'});
            }
            else{
                let student = {
                    name : name,
                    fname : fname,
                    DOB : DOB,
                    mobile : mobile,
                    address :address,
                    class : Class,
                    rollNo : rollNo,
                    fee : fee
                }
    
                var options =  {
                    method :'POST',
                    body : JSON.stringify(student),
                    headers : {
                        'Content-Type' : "application/json"
                    }
                }
    
                fetch('/addStudents',options)
                .then((res) => res.json())
                .then((json) => {
                    console.log(json);
                })
                .catch((err) => console.log(err))
    
                
                document.getElementById("name").value = null;
                document.getElementById("father_name").value = null;
                document.getElementById("DOB").value = null;
                document.getElementById("mobile").value = null;
                document.getElementById("address").value = null;
                document.getElementById("class").value = null;
                document.getElementById("roll_no").value = null;
                document.getElementById("fee").value = null;
    
                M.toast({html: 'Student is Added'})
            }


            
            
        }
        return (
            <div>
                <Header name="Add A New Student" />
                <div class="row container">
                    <h3>Enter Students Details</h3>
                    <form className="col s12 ">
                        <div className="row">
                            <div className="input-field col s6">
                                <input id="name" type="text" className="validate"/>
                                <label for="name">Students Name</label>
                            </div>
                                <div className="input-field col s6">
                                    <input id="father_name" type="text" className="validate"/>
                                        <label for="faher_name">Fathers Name</label>
                            </div>
                        </div>
                        <div className="row">
                            <div className="input-field col s6">
                                <input id="DOB" type="date" className="validate"/>
                                <label for="DOB">Date Of Birth</label>
                            </div>
                            <div className="input-field col s6">
                                <input id="mobile" type="number" className="validate"/>
                                <label for="mobile">Mobile</label>
                            </div>
                        </div>
                        <div className="row">
                            <div className="input-field col s12">
                                <input id="address" type="text" className="validate"/>
                                <label for="address">Home Address</label>
                            </div>
                        </div>
                        <div class="row">
                            <div className="input-field col s6">
                                <input id="class" type="number" className="validate"/>
                                <label for="class">Class</label>
                            </div>
                            <div className="input-field col s6">
                                <input id="roll_no" type="number" className="validate"/>
                                <label for="roll_no">Roll Number</label>
                            </div>
                        </div>
                        <div className="row">
                            <div className="input-field col s6">
                                <input id="fee" type="number" className="validate"/>
                                <label for="fee">Fee</label>
                            </div>
                        </div>                        
                    </form>
                    <a className="waves-effect waves-light btn blue"  onClick={addStudent}>Add Student</a>
                </div>


                                </div>
                                )
    }

}

export default AddStudent;