import React, { Component } from 'react';
import Header from "./Header";
import M from "materialize-css"


class Teacherdetail extends Component {
   state = {
     teachers : []
   } 
  render() {
    let teacherDetail = () =>{

      var options =  {
          method :'GET',
          headers : {
              'Content-Type' : "application/json"
          }
      }

      fetch('/getTeachers',options)
      .then((res) => res.json())
      .then((json) => {
          this.setState({teachers : json});
          M.toast({html: 'Showing details'})
          console.log(this.state.teachers)
      })
      .catch((err) => console.log(err))

      
      
      }

    
  return (
    <div >
      <Header name="Teachers Detail"/>
      
      <div className="container">
        <h3>Detail of Teachers</h3><br/><br/>
        <a className="waves-effect waves-light btn blue"  onClick={teacherDetail}>Show Teachers</a><br/><hr/><br/>
        <table>
            <thead>
            <tr>
                <th>Teacher Name</th>
                <th>Qualification</th>
                <th>Email</th>
                <th>Mobile</th>
                <th>Pay</th>
                <th>DOB</th>
                <th>Address</th>
            </tr>
            </thead>

            <tbody>

            {this.state.teachers.map((value,index)=>{
                return (
                <tr key={index}>
                <td>{value.name}</td>
                <td>{value.qualification}</td>
                <td>{value.email}</td>
                <td >{value.mobile}</td>
                <td >{value.pay}</td>
                <td >{value.DOB}</td>
                <td >{value.address}</td>
                </tr> 
                )
            })}
            
            
            </tbody>
            </table>
    </div> 
      
    </div>
  );
}
}

export default Teacherdetail;
