import React, { Component } from 'react';
import { Link } from 'react-router-dom';


import Header from './Header.js';


import details_pic from '../images/details.jpg'; 
import fee_pic from '../images/fee.jpg'; 
import student_pic from '../images/addstudent.jpg'; 
import student_expel from '../images/expelstudent.jpg'; 



class Students extends Component {



  render() {

    let data = [
      {
        name : 'Personal Deatails',
        details : "Student's Details",
        image : details_pic,
        link : '/studentDetail'
      },
      {
        name : 'Fee Details',
        details : 'Fee details of Student',
        image : fee_pic,
        link : '/studentFee'
      },
      {
        name : 'Add Student',
        details : 'Add A New Student',
        image : student_pic,
        link : '/addStudent'
      },
      {
        name : 'Expel Student',
        details : 'Expel A Student',
        image : student_expel,
        link : '/expelStudent'
      }
    ]

  

    return (
      <div>
        <Header name="STUDENT"/>
        <div className="container row">
        
{data.map(
  (value,index) =>{
    return (
      <div className="col s12 m5 l4 cardSetting center">
          <div className="card horizontal cardSetting">
            <div className="card-image">
              <img className="cardImage" src={value.image} />
            </div>
            <div className="card-stacked">
              <div className="card-content">
                <p>{value.details}</p>
              </div>
              <div className="card-action">
                <Link to={value.link} className="#">{value.name}</Link>
              </div>
            </div>
          </div>
        </div>
    )
  }
)}
      </div>
      </div>

      
      

    );
  }
}

export default Students;
