import React, { Component } from 'react';
import Header from "./Header";
import M from 'materialize-css';


class ExpelTeacher extends Component {
    
  render() {

    let expelTeacher = () =>{
      let name = document.getElementById('tname').value;

      if(!name){
       return M.toast({html: 'Enter name to Expel teacher'});
      }
      else{
      var options =  {
          method :'POST',
          body : JSON.stringify({name : name}),
          headers : {
              'Content-Type' : "application/json"
          }
      }

      fetch('/expelTeacher',options)
      .then((res)=> res.text())
      .then((json) => {

          M.toast({html: json})
      })
      .catch((err) => console.log(err))

      
      document.getElementById("tname").value = null;

      
      }
    }

    return (
      <div >
        <Header name="Expel Student"/>
        <div className="row container">
                    <h3>Enter Teachers name to Expel</h3>
                    <form className="col s12 ">
                        <div className="row">
                            <div className="input-field col s6">
                                <input id="tname" type="text" className="validate"/>
                                <label for="tname">Teacher Name</label>
                            </div>
                        </div>                        
                    </form>
                    <a className="waves-effect waves-light btn red" onClick={expelTeacher}>Expel Teacher</a>
                </div>
      </div>
    );
  }
}

export default ExpelTeacher;
